function MarqueeMern(){
    return(
        <Marquee autoFill true className="bg-dark"> 
            <Imongo className="m-4"/>
            <IExpress className="m-4"/>
            <Ireact className="m-4"/>
            <Inode className="m-4"/>
        </Marquee>
    )
}
