import React, {useEffect, useState} from "react";
import { useParams, useNavigate, Link } from "react-router-dom";


export default function SinglePost(){
    const [post, setPost] = useState({
        posts:[],
    });
    const params = useParams();
    const navigate = useNavigate();
    useEffect(()=>{
        async function fetchData(){
            //Ici je signale que je veux rcuprer les paramtres de mes posts en fonction de l'id
            const id= params.id.toString();
            //Je dfinis l'url o je vais rcupre mes donnes via l'id.
            const response= await fetch(`http://localhost:4000/post/${id}`);
            if(!response.ok){
                //si je n'ai pas de rponse, un message d'erreur s'affiche et le processus s'arrete
                const message =`An error has occured:${response.statusText}`;
                window.alert(message);
                return;
            };
            //je stock dans une variable l'attente de la rponse.
            const post = await response.json();
            //Si j'ai une rponse mais pas l'id dsir, j'affiche un message d'erreur.
            if(!post){
                window.alert(`post with id ${id} not found`);
                navigate("/blog");
                return;
            };
            setPost(post);
            };
            //je fais ensuite un callback de notre fonction.
            fetchData();
            return;
        }, [params.id, navigate]);

        const title =post.title;
        const content = post.content;
        return(
            <div className="container-fluid p-4">
                    <h1 className="p-4">{title}</h1>
                    <div className="p-4">{content}</div>
                    <br/>
                <button className="btn btn-dark m-4"><Link className="nav-link" to="/blog">Retour</Link></button>
            </div>
        )
    }

