import React,{ useEffect, useState } from "react";
import { Link } from "react-router-dom";
import {useSelector} from "react-redux";

//ici je configure la mthode GET qui nous permettra d'afficher nos diffrents posts.
//Chaque post proviendra de donnes stockes dans la base de donnes.
// ce la je configure des routes qui vont nous permettre de modifier ou supprimer des posts.
const Post = (props)=>(
    <tr>
        <td className="p-4"><Link to={`/single/${props.post._id}`} state={{id : props.post._id}}>{`${props.post.title.substring(0,15)}...`}</Link></td>
        <td className="p-4">{`${props.post.content.substring(0,12)}...`}</td>
        <td>
            <Link className="btn btn-dark mt-2" to={`/edit/${props.post._id}`}>Editer</Link>
            <button className="btn btn-danger mt-2"
                onClick={()=>{
                    props.deletePost(props.post._id);
                }}
                >
                Supprimer
                </button>
        </td>

    </tr>
);

// Je cre notre list de posts qui seront visibles par tout le monde.
export default function PostList(){
    const [posts, setPosts]= useState([]);// je cre un tat qui permet de slectionner notre collection de posts dans la bdd.
//avec l'effet ci-dessous je vais rcuprer nos effets dans la base de donnes.
    const { userInfo } = useSelector((state) => state.auth);
    useEffect(()=>{
        async function getPosts(){
            // je vais requeter nos posts.
            const response = await fetch(`http://localhost:4000/post/`);
            //si pas de rponse alors Erreur.
            if (!response.ok){
                const message = `An error occurred: ${response.statusText}`;
                window.alert(message);
                return;
            }
            //je stock dans notre variable notre rponse.
            const posts = await response.json();
            //j'appelle notre tat afin de rcuprer nos posts.
            setPosts(posts);
        }
        //je fais un call back de notre fonction qui nous permettra de voir nos posts.
        getPosts();
        return;
    }, [posts.lenght]);

    //je cr maintenant une mthode pour supprimer nos posts.
    async function deletePost(id){
        //je rcupre l'id du post que je veux supprimer
        await fetch(`http://localhost:4000/post/${id}`,{
            //j'y ajoute la mthode de supression.
            method: "DELETE"
        });
        const newPosts = posts.filter((el)=>el._id !== id);
        setPosts(newPosts);
    }
    // avec la mthode suivante j'instaure le mappage de nos prcdentes mthodes.
    function postList(){
        return posts.map((post)=>{
            return(
                <Post
                post = {post}
                deletePost = {()=>deletePost(post._id)}
                key =  {post._id}
                />
            );
        });
    }
    
    // Je cr ensuite la section html qui va recueillir nos posts.*
    return(
        <div>
                {userInfo ?
                (
                    <>
                        <div className="d-flex justify-content-between p-4 ">
                            <h3>LISTE DE POSTS</h3>         
                            <Link className="btn btn-primary" to="/create">
                                Nouveau Post
                            </Link>
                        </div>
                        <div className="container-fluid">
                            <table className="table table-hover " style={{marginTop:20}}>
                                <thead >
                                    <tr>
                                        <th className="bg-dark-subtle p-4">Titre</th>
                                        <th className="bg-dark-subtle p-4">Contenu</th> 
                                    </tr>
                                </thead>
                                <tbody className="table-group-divider">{postList()}</tbody>
                            </table>
                        </div>
                    </>
                ):(
                    <>
                    <div className="card  m-5 p-5">
                        <h3 className="text-uppercase">Authentification ncessaire</h3>
                        <p>
                                    Il est ncessaire d'tre connect pour accder au blog.
                        </p>
                            <div className="d-flex">
                                <Link className="btn btn-dark " to="/login">
                                        Se connecter
                                </Link>
                            </div>
                    </div>
                    </>
                )}
        </div>
    )
}