import  React, {useState} from "react";
import {Link, useNavigate} from "react-router-dom";

//Cette fonction va nous permettre de crer un nouveau post.
export default function Create(){
        //ici je cr mon composant de cration
        //je stock dans un tableau les lments
        //ces lkments seront modifis avec des fonction annexes. 
        const [form, setForm]= useState({
            title:"",
            content:"",
        });

        
    const navigate = useNavigate();
    //On va mettre a jour les proprits de l'etat via cette fonction
    function updateForm(value){
        return setForm((prev)=>{
            return{...prev,...value};
        });
    };

//Cette fonction est utilise pour appliquer des changements  notre DB
// On va crer un nouveau post lorqu'on soumet notre requete
    async function onSubmit(e){
        e.preventDefault();
        // Je stock dans cette variable les donnes de notre tat form
        const newPost ={...form};
        //je rcupre l'adresse serveur side ou je vais envoyer les donnes de la bd
        await fetch ("http://localhost:4000/post",{
            method:"POST",
            headers:{
                "Content-Type": "application/json",
            },
            body:JSON.stringify(newPost),
        })
        //en cas d'rreurs j'affiche un message d'erreur.
        .catch(error=>{
            window.alert(error);
            return;
        });
        //On oublie pas de dfinir les donnes qui seront modifies  la soumissions de notre formulaire
        setForm({title:"", content:""});
        // Je dfinit ensuite notre navigation post soumission.
        navigate("/blog");
    };

    //formulaire pour renseigner les nouvelles informations de cration de post.
    return(
        <div className="container-fluid p-5">
            <h3 className="text-toUpperCase">CREER UN NOUVEAU POST</h3>
            <form onSubmit={onSubmit}>
                <div className="form-group mt-4">
                    <label htmlFor="title">Titre:</label>
                    <input
                        type="text"
                        className="form-control"
                        id="title"
                        value={form.title}
                        onChange={(e)=>updateForm({title: e.target.value})}
                    />
                </div>
                <div className="form-group mt-4">
                    <label htmlFor="content">Contenu:</label>
                    <input
                    type="text"
                    className="form-control"
                    id="content"
                    value={form.content}
                    onChange={(e)=>updateForm({content: e.target.value})}
                />
                </div>
                <div className="d-flex mt-4">
                    <div className="form-group">
                        <input
                        type="submit"
                        value="Creer Post"
                        className="btn btn-primary "
                        />
                    </div>
                    <button className="btn btn-secondary ms-3">
                        <Link className="nav-link" to="/blog">Retour</Link>
                    </button>
                </div>
            </form>
        </div>
    );

};